<template>
	<div class="index-page">
		<div class="page-header" @click="toPersonal">
			<div class="avatar">
				<van-image :src="getAvatar">
					<template v-slot:loading>
						<van-loading type="spinner" size="20" />
					</template>
				</van-image>
			</div>
			<div class="info">
				<div class="name">{{info.realName}}</div>
				<div class="phone">{{info.phone}}</div>
			</div>
			<van-icon name="arrow" color="#dddddd" size="0.5rem"/>
			<!--<a class="logout" @click="onLogout">退出</a>-->
		</div>
		<van-panel title="我的培训">
			<van-loading v-if="loading" type="spinner" size="24" />
			<ul v-else class="data-list">
				<div v-if="nodata" class="no-data"></div>
				<template v-else>
					<li v-for="(item, i) in train" :key="i" @click="onClick(item)">
						<div class="item-img">
							<van-image :src="item.img">
								<template v-slot:loading>
									<van-loading type="spinner" size="20" />
								</template>
							</van-image>
						</div>
						<div class="item-content">
							<div class="title">{{item.cultivateName}}</div>
							<div class="time">培训时间：{{formatDate(item.beginTime)}}~{{formatDate(item.endTime)}}</div>
							<!--<div class="more">
			  				<span>总分：{{item.totalScore}}</span>
			  				<span>及格分：{{item.passScore}}</span>
			  				<span>成绩：{{item.realScore}}</span>
			  			</div>-->
						</div>
						<van-icon name="arrow" />
					</li>
				</template>
			</ul>
		</van-panel>
	</div>
</template>

<script>
	import { mapState } from "vuex"
	const avatar1 = require('../../assets/images/mail.png')
	const avatar2 = require('../../assets/images/femail.png')
	const icon = require('../../assets/images/train.png')
	export default {
		name: 'index',
		data() {
			return {
				loading: true,
				nodata: false,
				train: []
			}
		},
		computed: {
			...mapState(['info']),
			getAvatar() {
				return this.info.sex ? avatar2 : avatar1
			}
		},
		mounted() {
			this.getData()
		},
		methods: {
			formatDate(date) {
				date = this.$Utils.toDate(date)
				return this.$Utils.dateFtt('yyyy-MM-dd', date)
			},
			// 获取培训情况
			async getData() {
				let params = {
					studentId: this.info.id
				}
				let res = await this.$Request.get('/student/querySCByWeChat.do', params)
				this.loading = false
				if(res && res.code === 0) {
					let data = res.list || []
					this.nodata = !data.length
					data.forEach(item => { item.img = icon })
					this.train = data
				} else {
					this.nodata = true
				}
			},
			// 退出
			/*onLogout() {
			},*/
			toPersonal() {
				this.$router.push({
					path: '/personal'
				})
			},
			onClick(item) {
				this.$router.push({
					path: '/guide',
					query: {
						id: item.cultivateId
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>

</style>


